import React from 'react'
import { get } from 'lodash'

import Box from '../components/Box'
import Flex from '../components/Flex'
import Link from '../components/Link'
import Button from '../components/Button'
import useCsvButton from './useCsvButton'

const BottomButton = () => {
  const buttons = useCsvButton()
  return (
    <Flex position="fixed" bottom="0" left="0" right="0" bg="custom.blue" zIndex={99}>
      {get(buttons, ['data'], []).slice(2).map(({ name, link }, i) => (
        <Box width="50%" key={i}>
          <Button.Transparent
            width="100%"
            as={Link}
            href={link}
            borderLeft={i && '1px solid white'}
            borderRadius="0"
          >{name}</Button.Transparent>
        </Box>
      ))}
    </Flex>
  )
}

export default BottomButton
